import { Component, OnInit, ViewChild } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { DailyGiftManagementService } from './daily-gift-management.service';
import { UtilsService } from 'src/app/services/utils.service';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { ModalEditDailyGiftComponent } from 'src/app/components/modal-edit-daily-gift/modal-edit-daily-gift.component';

@Component({
  selector: 'app-daily-gift-management',
  templateUrl: './daily-gift-management.page.html',
  styleUrls: ['./daily-gift-management.page.scss'],
})
export class DailyGiftManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;
  constructor(private popoverController: PopoverController,
    public dailyGiftService: DailyGiftManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) { }

  ngOnInit() {
  }
  
  showDetail(data: any, mode: any) {
    if (mode == "edit") {
      this.showModalEditDailyGift(data.ev, data.row._row.data);
    }
    else {
      this.showModalEditDailyGift(null, null);
    }
  }

  async showModalEditDailyGift(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalEditDailyGiftComponent,
      id: "editDailyFreeGiftModal",
      event: ev,
      translucent: true,
      componentProps: { rowProduct: row },
      cssClass: "edit-daily-free-gift-popover"
    });
    return await popover.present();
  }
  ionViewDidEnter() {
    let removeHeight = [];
    this.appValueService.APP_TABULATOR_HEIGHT = [this.contentSearch, removeHeight];
    this.dailyGiftService.heightTable = this.utilsService.settingTableHeight(this.appValueService.APP_TABULATOR_HEIGHT);
  }
}
